import './App.css'
import React, {Suspense} from 'react';
import ZestyBanner from './zesty-r3f-sdk';
import { BoxBufferGeometry, CubeTextureLoader, Mesh, MeshBasicMaterial, PlaneBufferGeometry } from 'three';
import { useThree } from '@react-three/fiber';
import { PointerLockControls } from '@react-three/drei';
import { VRCanvas, DefaultXRControllers } from '@react-three/xr'

const Player = () => {
  const { camera } = useThree();
  camera.position.set(0, 1.6, 0);
  return null;
}

const SkyBox = () => {
  const { scene } = useThree();
  const loader = new CubeTextureLoader();
  // The CubeTextureLoader load method takes an array of urls representing all 6 sides of the cube.
  const texture = loader.load([
    "../../res/skybox/posx.png",
    "../../res/skybox/negx.png",
    "../../res/skybox/posy.png",
    "../../res/skybox/negy.png",
    "../../res/skybox/posz.png",
    "../../res/skybox/negz.png",
  ]);
  // Set the scene background property to the resulting texture.
  scene.background = texture;
  return null;
}

const Ground = () => {
  const material = new MeshBasicMaterial({ color: 0x567d46 });
  const geometry = new PlaneBufferGeometry(256, 256);
  return (
    <mesh 
      material={material} 
      geometry={geometry} 
      rotation={[-Math.PI / 2, 0, 0]} />
  );
}

const Wall = props => {
  const material = new MeshBasicMaterial();
  const geometry = new BoxBufferGeometry(3, 3, .25);
  return (
    <mesh 
      material={material} 
      geometry={geometry}
      position={props.position}
      rotation={props.rotation} />
  );
}

